
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"86%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator=">" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="8">
					<el-form-item label="学校账号" prop="xuexiaozhanghao">
						<el-input class="list_inp" v-model="form.xuexiaozhanghao" placeholder="学校账号"
							 type="text" 							:readonly="!isAdd||disabledForm.xuexiaozhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="学校名称" prop="xuexiaomingcheng">
						<el-input class="list_inp" v-model="form.xuexiaomingcheng" placeholder="学校名称"
							 type="text" 							:readonly="!isAdd||disabledForm.xuexiaomingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="招生计划" prop="zhaoshengjihua">
						<el-input class="list_inp" v-model="form.zhaoshengjihua" placeholder="招生计划"
							 type="text" 							:readonly="!isAdd||disabledForm.zhaoshengjihua?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="联系电话" prop="lianxidianhua">
						<el-input class="list_inp" v-model="form.lianxidianhua" placeholder="联系电话"
							 type="text" 							:readonly="!isAdd||disabledForm.lianxidianhua?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="学校地址" prop="xuexiaodizhi">
						<el-input class="list_inp" v-model="form.xuexiaodizhi" placeholder="学校地址"
							 type="text" 							:readonly="!isAdd||disabledForm.xuexiaodizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="录取分数线" prop="luqufenshuxian">
						<el-input class="list_inp" v-model.number="form.luqufenshuxian" placeholder="录取分数线"
							 type="text" 							:readonly="!isAdd||disabledForm.luqufenshuxian?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="省份" prop="shengfen">
						<el-input class="list_inp" v-model="form.shengfen" placeholder="省份"
							 type="text" 							:readonly="!isAdd||disabledForm.shengfen?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="学费标准" prop="xuefeibiaozhun">
						<el-input class="list_inp" v-model.number="form.xuefeibiaozhun" placeholder="学费标准"
							 type="text" 							:readonly="!isAdd||disabledForm.xuefeibiaozhun?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="报名时间" prop="baomingshijian">
						<el-date-picker
							class="list_date"
							v-model="form.baomingshijian"
							format="YYYY 年 MM 月 DD 日"
							value-format="YYYY-MM-DD"
							type="datetime"
							:readonly="!isAdd||disabledForm.baomingshijian?true:false"
							placeholder="请选择报名时间"
							style="width: 100%;" />
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="考试时间" prop="kaoshishijian">
						<el-date-picker
							class="list_date"
							v-model="form.kaoshishijian"
							format="YYYY 年 MM 月 DD 日"
							value-format="YYYY-MM-DD"
							type="datetime"
							:readonly="!isAdd||disabledForm.kaoshishijian?true:false"
							placeholder="请选择考试时间"
							style="width: 100%;" />
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="填报专业" prop="tianbaozhuanye">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.tianbaozhuanye?true:false"
							v-model="form.tianbaozhuanyes" 
							placeholder="请选择填报专业"
							multiple
							style="width:100%;"
							@change="zhiyuantianbaotianbaozhuanyeChange">
							<el-option v-for="(item,index) in tianbaozhuanyeLists" :label="item" :value="item">
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="学生账号" prop="xueshengzhanghao">
						<el-input class="list_inp" v-model="form.xueshengzhanghao" placeholder="学生账号"
							 type="text" 							:readonly="!isAdd||disabledForm.xueshengzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="学生姓名" prop="xueshengxingming">
						<el-input class="list_inp" v-model="form.xueshengxingming" placeholder="学生姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.xueshengxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="手机号码" prop="shoujihaoma">
						<el-input class="list_inp" v-model="form.shoujihaoma" placeholder="手机号码"
							 type="text" 							:readonly="!isAdd||disabledForm.shoujihaoma?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="填报时间" prop="tianbaoshijian">
						<el-date-picker
							class="list_date"
							v-model="form.tianbaoshijian"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.tianbaoshijian?true:false"
							placeholder="请选择填报时间" />
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="考试总分" prop="kaoshizongfen">
						<el-input class="list_inp" v-model.number="form.kaoshizongfen" placeholder="考试总分"
							 type="number" 							:readonly="!isAdd||disabledForm.kaoshizongfen?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'zhiyuantianbao'
	const formName = '志愿填报'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		xuexiaozhanghao: '',
		xuexiaomingcheng: '',
		zhaoshengjihua: '',
		lianxidianhua: '',
		xuexiaodizhi: '',
		luqufenshuxian: 0,
		shengfen: '',
		xuefeibiaozhun: 0,
		baomingshijian: '',
		kaoshishijian: '',
		tianbaozhuanye: [],
		xueshengzhanghao: '',
		xueshengxingming: '',
		shoujihaoma: '',
		tianbaoshijian: '',
		kaoshizongfen: 0,
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		xuexiaozhanghao : false,
		xuexiaomingcheng : false,
		zhaoshengjihua : false,
		lianxidianhua : false,
		xuexiaodizhi : false,
		luqufenshuxian : false,
		shengfen : false,
		xuefeibiaozhun : false,
		baomingshijian : false,
		kaoshishijian : false,
		tianbaozhuanye : false,
		xueshengzhanghao : false,
		xueshengxingming : false,
		shoujihaoma : false,
		tianbaoshijian : false,
		kaoshizongfen : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		xuexiaozhanghao: [
		],
		xuexiaomingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		zhaoshengjihua: [
		],
		lianxidianhua: [
		],
		xuexiaodizhi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		luqufenshuxian: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		shengfen: [
		],
		xuefeibiaozhun: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		baomingshijian: [
		],
		kaoshishijian: [
		],
		tianbaozhuanye: [
		],
		xueshengzhanghao: [
		],
		xueshengxingming: [
		],
		shoujihaoma: [
		],
		tianbaoshijian: [
		],
		kaoshizongfen: [
			{ validator: validateNumber, trigger: 'blur' },
		],
	})
	//填报专业列表
	const tianbaozhuanyeLists = ref([])
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			if(res.data.data.tianbaozhuanye){
				res.data.data.tianbaozhuanyes = res.data.data.tianbaozhuanye.split(",")
			}else{
				res.data.data.tianbaozhuanyes = []
			}
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
			form.value.tianbaoshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='xuexiaozhanghao'){
					form.value.xuexiaozhanghao = row[x];
					disabledForm.value.xuexiaozhanghao = true;
					continue;
				}
				if(x=='xuexiaomingcheng'){
					form.value.xuexiaomingcheng = row[x];
					disabledForm.value.xuexiaomingcheng = true;
					continue;
				}
				if(x=='zhaoshengjihua'){
					form.value.zhaoshengjihua = row[x];
					disabledForm.value.zhaoshengjihua = true;
					continue;
				}
				if(x=='lianxidianhua'){
					form.value.lianxidianhua = row[x];
					disabledForm.value.lianxidianhua = true;
					continue;
				}
				if(x=='xuexiaodizhi'){
					form.value.xuexiaodizhi = row[x];
					disabledForm.value.xuexiaodizhi = true;
					continue;
				}
				if(x=='luqufenshuxian'){
					form.value.luqufenshuxian = row[x];
					disabledForm.value.luqufenshuxian = true;
					continue;
				}
				if(x=='shengfen'){
					form.value.shengfen = row[x];
					disabledForm.value.shengfen = true;
					continue;
				}
				if(x=='xuefeibiaozhun'){
					form.value.xuefeibiaozhun = row[x];
					disabledForm.value.xuefeibiaozhun = true;
					continue;
				}
				if(x=='baomingshijian'){
					form.value.baomingshijian = row[x];
					disabledForm.value.baomingshijian = true;
					continue;
				}
				if(x=='kaoshishijian'){
					form.value.kaoshishijian = row[x];
					disabledForm.value.kaoshishijian = true;
					continue;
				}
				if(x=='tianbaozhuanye'){
					form.value.tianbaozhuanye = row[x];
					disabledForm.value.tianbaozhuanye = true;
					continue;
				}
				if(x=='xueshengzhanghao'){
					form.value.xueshengzhanghao = row[x];
					disabledForm.value.xueshengzhanghao = true;
					continue;
				}
				if(x=='xueshengxingming'){
					form.value.xueshengxingming = row[x];
					disabledForm.value.xueshengxingming = true;
					continue;
				}
				if(x=='shoujihaoma'){
					form.value.shoujihaoma = row[x];
					disabledForm.value.shoujihaoma = true;
					continue;
				}
				if(x=='tianbaoshijian'){
					form.value.tianbaoshijian = row[x];
					disabledForm.value.tianbaoshijian = true;
					continue;
				}
				if(x=='kaoshizongfen'){
					form.value.kaoshizongfen = row[x];
					disabledForm.value.kaoshizongfen = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('xueshengzhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.xueshengzhanghao = json.xueshengzhanghao
				disabledForm.value.xueshengzhanghao = true;
			}
			if(json.hasOwnProperty('xueshengxingming') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.xueshengxingming = json.xueshengxingming
				disabledForm.value.xueshengxingming = true;
			}
			if(json.hasOwnProperty('shoujihaoma') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shoujihaoma = json.shoujihaoma
				disabledForm.value.shoujihaoma = true;
			}
		})
		context?.$http({
			url: `option/zhuanyeleixing/zhuanyeleixing`,
			method: 'get'
		}).then(res=>{
			tianbaozhuanyeLists.value = res.data.data
		})
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//多选框数组转字符串
	const zhiyuantianbaotianbaozhuanyeChange= ()=> {
		form.value.tianbaozhuanye = form.value.tianbaozhuanyes.join(',')
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 12px 20px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
