import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import zhaoshengxinxiList from '@/views/pages/zhaoshengxinxi/list'
import zhaoshengxinxiDetail from '@/views/pages/zhaoshengxinxi/formModel'
import zhaoshengxinxiAdd from '@/views/pages/zhaoshengxinxi/formAdd'
import zhiyuantianbaoList from '@/views/pages/zhiyuantianbao/list'
import zhiyuantianbaoDetail from '@/views/pages/zhiyuantianbao/formModel'
import zhiyuantianbaoAdd from '@/views/pages/zhiyuantianbao/formAdd'
import messagesList from '@/views/pages/messages/list'
import forumList from '@/views/pages/forum/list'
import zhuanyeleixingList from '@/views/pages/zhuanyeleixing/list'
import zhuanyeleixingDetail from '@/views/pages/zhuanyeleixing/formModel'
import zhuanyeleixingAdd from '@/views/pages/zhuanyeleixing/formAdd'
import shengfenList from '@/views/pages/shengfen/list'
import shengfenDetail from '@/views/pages/shengfen/formModel'
import shengfenAdd from '@/views/pages/shengfen/formAdd'
import xuexiaoxinxiList from '@/views/pages/xuexiaoxinxi/list'
import xuexiaoxinxiDetail from '@/views/pages/xuexiaoxinxi/formModel'
import xuexiaoxinxiAdd from '@/views/pages/xuexiaoxinxi/formAdd'
import xuexiaoxinxiRegister from '@/views/pages/xuexiaoxinxi/register'
import xuexiaoxinxiCenter from '@/views/pages/xuexiaoxinxi/center'
import luquxinxiList from '@/views/pages/luquxinxi/list'
import luquxinxiDetail from '@/views/pages/luquxinxi/formModel'
import luquxinxiAdd from '@/views/pages/luquxinxi/formAdd'
import newsList from '@/views/pages/news/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'zhaoshengxinxiList',
			component: zhaoshengxinxiList
		}, {
			path: 'zhaoshengxinxiDetail',
			component: zhaoshengxinxiDetail
		}, {
			path: 'zhaoshengxinxiAdd',
			component: zhaoshengxinxiAdd
		}
		, {
			path: 'zhiyuantianbaoList',
			component: zhiyuantianbaoList
		}, {
			path: 'zhiyuantianbaoDetail',
			component: zhiyuantianbaoDetail
		}, {
			path: 'zhiyuantianbaoAdd',
			component: zhiyuantianbaoAdd
		}
		, {
			path: 'messagesList',
			component: messagesList
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'zhuanyeleixingList',
			component: zhuanyeleixingList
		}, {
			path: 'zhuanyeleixingDetail',
			component: zhuanyeleixingDetail
		}, {
			path: 'zhuanyeleixingAdd',
			component: zhuanyeleixingAdd
		}
		, {
			path: 'shengfenList',
			component: shengfenList
		}, {
			path: 'shengfenDetail',
			component: shengfenDetail
		}, {
			path: 'shengfenAdd',
			component: shengfenAdd
		}
		, {
			path: 'xuexiaoxinxiList',
			component: xuexiaoxinxiList
		}, {
			path: 'xuexiaoxinxiDetail',
			component: xuexiaoxinxiDetail
		}, {
			path: 'xuexiaoxinxiAdd',
			component: xuexiaoxinxiAdd
		}
		, {
			path: 'xuexiaoxinxiCenter',
			component: xuexiaoxinxiCenter
		}
		, {
			path: 'luquxinxiList',
			component: luquxinxiList
		}, {
			path: 'luquxinxiDetail',
			component: luquxinxiDetail
		}, {
			path: 'luquxinxiAdd',
			component: luquxinxiAdd
		}
		, {
			path: 'newsList',
			component: newsList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
	,{
		path: '/xuexiaoxinxiRegister',
		component: xuexiaoxinxiRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
